.completeButton {
  height: 43px;
  width: 100%;
  margin: 20px 0;

  background-color: #343A40;
  color: #fff;
  font-size: 20px;
  border: 1px solid transparent;

  transition: all ease-in-out 0.2s;

  &:hover {
    background-color: #fff;
    color: #343A40;
    border: 1px solid #343A40;
  }

  &:focus {
    transform: scale(0.95);
  }

  &-disable {
    background-color: #868E96;
  }
}